<template>
  <div id="followUpDetail" v-loading.fullscreen.lock="loading" element-loading-text="">
    <h1></h1>
  </div>
</template>

<script type="text/ecmascript-6">

export default {
  props: ["proid"],
  data () {
    return {
      pro_id: '',
      loading:false,//
    }
  },
  methods: {

  },
  created(){

  },
  watch : {
    proid : function(e){
      this.pro_id=e;

    },//id
  }
}
</script>

<style lang="less">
  @import '../../../assets/css/followUpDetail';
</style>



