<template>
  <div id="alertContactsDetail" v-loading.fullscreen.lock="loading" element-loading-text="">
    <!--========================================================================================-->
    <el-dialog :visible="dialogConVisible" custom-class="dialogCon" :before-close="handleClose" close-on-press-escape close-on-click-modal>
      <div class="contain-grid contain-center1 fl">
        <div class="main-box clearfix">
          <div class="item-lists clearfix">
            <div class="item-list item-lists-top clearfix">
              <div class="item">
                <div class="name fl">{{contacts.card_name}}</div>
                <div class="career fl">{{contacts.card_company_career}}</div>
              </div>
              <div class="item" style="margin-bottom: 55px;">
                <div class="title">{{contacts.card_nickname}}</div>
              </div>
              <div class="header fr">
                <img src="../../../assets/images/header.png">
              </div>
              <div class="item com"><img src="../../../assets/images/company.png">{{contacts.card_company_name}} | {{contacts.card_brand}}</div>
              <div class="item com"><img src="../../../assets/images/phone.png">{{contacts.card_mobile}}</div>
              <div class="item com" style="width: 780px;">
                <img src="../../../assets/images/email.png">{{contacts.card_email}}
                <div class="fr">
                  : 
                </div>
              </div>

            </div>
            <div class="item-list item-list-main">
              <!---->
              <div class="item">
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/tag.png"></span>
                  <span class="edit fr"><img class="img" src="../../../assets/images/editTo.png"></span>
                </div>
                <div class="block">
                  <div class="tag ">90</div>
                  <div class="tag ">90</div>
                </div>
              </div>
              <!---->
              <!---->
              <div class="item" >
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/projectColl.png"></span>
                </div>
                <div class="ul_lists clearfix" v-loading.body="loading1" element-loading-text="">
                  <div class="list" v-for="projectList in projectLists">
                    <div class="li title">{{projectList.pro_intro}}</div>
                    <div class="img" v-show="projectList.is_exclusive==''"><img src="../../../assets/images/dujia.png"></div>
                    <div class="img" v-show="projectList.is_exclusive==''"><img src="../../../assets/images/feidujia.png"></div>
                    <div class="li">
                      <span class="tags" v-for="industry in projectList.pro_industry">{{industry}}</span>
                    </div>
                    <div class="li" style="margin-top: 16px;">
                      <span class="big-tag" v-show="projectList.pro_scale!='-'">{{projectList.pro_scale}}</span><span class="split"  v-show="projectList.pro_scale!='-'"></span>
                      <span class="big-tag" v-show="projectList.pro_finance_stock_after!='-'">{{projectList.pro_finance_stock_after}}%</span><span class="split" v-show="projectList.pro_finance_stock_after!='-'"></span>
                      <span class="big-tag" v-show="projectList.pro_stage!='-'">{{projectList.pro_stage}}</span><span class="split" v-show="projectList.pro_stage!='-'"></span>
                      <span class="big-tag" v-show="projectList.pro_area!='-'">{{projectList.pro_area}}</span>
                    </div>
                  </div>
                </div>
                <el-pagination
                  class="pagination fr"
                  small
                  @current-change="filterChangeCurrent"
                  :current-page.sync="currentPage"
                  layout="prev, pager, next"
                  :page-size="10"
                  :total="totalData"
                  v-show="listShow">
                </el-pagination>
                <span class="b-hander" @click="closeDiv('listShow')" v-show="listShow"></span>
                <span class="b-hander" @click="openDiv('listShow')" v-show="!listShow"></span>
              </div>
              <!---->
              <div class="item">
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/anli.png"></span>
                </div>
                <div class="block lh">
                  <span class="radio"><img src="../../../assets/images/radioTag.png"></span>
                  <span class="time">2017.5</span>
                  <span class="tag_To">IOP</span>
                  <span class="title1"></span>
                  <span class="title2">100-500</span>
                  <span class="tags_To"></span>
                  <span class="address"></span>
                </div>
              </div>
              <!---->
              <div class="item">
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/miaoshu.png"></span>
                </div>
                <div class="block">
                  <div class="main">
                     1.  2. 7 24 
                  </div>
                </div>
              </div>
              <!---->
              <div class="item">
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/money2.png"></span>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>
                  <div class="tit_right fl">saas</div>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>
                  <div class="tit_right fl">Pre-AAB</div>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>
                  <div class="tit_right fl">1000-3000</div>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>

                </div>
                <div class="block">
                  <div class="tit_mian"> 1. </div>
                </div>
              </div>
              <!---->
              <div class="item">
                <div class="block clearfix" style="margin-bottom: 33px;">
                  <span class="title fl"><img class="img" src="../../../assets/images/ziyuan.png"></span>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>
                  <div class="tit_right fl">saas</div>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>
                  <div class="tit_right fl">Pre-AAB</div>
                </div>
                <div class="block tit clearfix">
                  <div class="tit_left fl">  </div>

                </div>
                <div class="block">
                  <div class="tit_mian"> 1. </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>

      <!--<div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleTo"> </el-button>
        <el-button type="primary" @click="dialogVisibleTo"> </el-button>
      </div>-->
    </el-dialog>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    props: ["dialogConVisible","proid"],
    data () {
      return {
        loading:false,//
        loading1:false,//2
        pro_id:'',
        listShow:true,//
        currentPage:1,//
        totalData:50,//
        contacts:{
          user_id:'',//id
          card_name:'',//
          card_nickname:'',//
          card_mobile:'18758307033',//
          card_email:'zhangsan@weitianshi.cn',//
          card_company_name:' ',//
          card_brand:'',//
          card_company_career:'',//
          contacts_tag:[],//

          pro_industry: [],//
          pro_stage: {
            "stage_id": '',
            "stage_name": "",
            "sort": 2,
            "created_at": null,
            "updated_at": null
          },//
          pro_finance_scale: '',//
          pro_area: {
            area_id: '',
            area_title: "",//
            pid: ''//
          },//1
          main:'',//
          give:[],//
          push:[],//
          describe:'',//
        },//
        projectLists:[
          {
            pro_intro:"",
            is_exclusive:"",
            pro_industry:[],
            pro_scale:"",
            pro_area:"",
            pro_stage:"",
            pro_finance_stock_after:""
          }
        ],//
        projectListsSmall:[],//2
        projectListsAll:[],//
      }
    },
    methods: {
      handleClose(){
        this.$emit('changeCon', false)
      },
      dialogVisibleTo(){
        this.$emit('changeCon', false)
      },
      openDiv(v){
        this[v] = true;
        this.projectLists=this.projectListsAll.slice(0);
      },
      closeDiv(v){
        this[v] = false;
        this.projectLists=this.projectListsSmall.slice(0);
      },
      filterChangeCurrent(page){
        this.getProjectList(page);
      },//1
      getProjectList(page){
        this.loading1=true;
        this.getPra.user_id=this.contacts.user_id;
//      this.getPra.user_id="2rzyz5vp";
        this.currentPage=page;
        this.getPra.page=page;
        this.$http.post(this.URL.getProjectList,this.getPra)
          .then(res=>{
            let data = res.data.data;
            this.projectListsAll=this.setProjectList(data);
            this.projectListsSmall=this.setProjectList(data).slice(0,2);
            if(this.listShow) this.projectLists=this.projectListsAll.slice(0);
            else this.projectLists=this.projectListsSmall.slice(0);
            this.loading1=false;
            this.totalData=res.data.count;
          })
          .catch(err=>{
            this.$tool.console(err,2);
            this.loading1=false;
            this.$tool.error("")
          })
      },//
    },
    created(){
//      this.getWxProjectCategory();
    },
    watch : {
      proid : function(e){
        this.pro_id=e;

      },//id
    }
  }
</script>

<style lang="less">
  @import '../../../assets/css/alertContactsDetail';
</style>




